export default {
	data() {
		return {
			drakIndex:"",
			shopname:"",
			address:"",
			door_photos:"",
			use_time:"",
			use_money:'',
			house_name:'',
			tui_money:'',
			cash:"",
			oid:'',
			status:'',
			disabled:true
		}
	},
	onLoad(e) {
		var oid = e.id
		this.oid = oid
		this.drawIndex(oid)
	},
	methods: {
		async drawIndex(id){
			const that = this
			const res = await this.$myRequest({
				method:"POST",
				url:"/backHouse?oid=" + id
			})
			that.shopname = res.data.data.shopname
			that.address = res.data.data.address
			that.door_photos = res.data.data.door_photos
			that.use_time = res.data.data.use_time
			that.house_name = res.data.data.house_name
			that.use_money = res.data.data.use_money
			that.tui_money = res.data.data.tui_money
			that.cash = res.data.data.price
		},
		async backPay(){
			const that = this
			if(that.tui_money == 0 ){
				uni.showToast({
					title:"退款金额为0"
				})
			}else{
				uni.showModal({
					title:"您确定退款退房",
					success(res) {
						if(res.confirm){
							uni.hideLoading()
							that.tuiPay()
						}else if (res.cancel){
							uni.hideLoading()
						}
					}
				})
			}
		},
		async tuiPay(){
			uni.showLoading({})
			const that = this
			const res = await this.$myRequest({
				method:"POST",
				url:"/backPay",
				data:{
					oid:that.oid,
					refund_fee:that.tui_money
				},
			})	
			that.status = res.data.status
			if(that.status == 1){
				this.disabled=false
				uni.navigateTo({
					url:"../payment_success_houe/payment_success_houe"
				})
			}else{
				uni.showToast({
					title:res.data.errmsg,
					icon:'none',
					duration:2000
				})
				// uni.navigateTo({
				// 	url:"../payment_failure_hosue/payment_failure_hosue",
				// })
			}
		}
	}
}
